___CSS_LOADER_EXPORT___.push([module.id, `/* General Styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f8f9fa;
  color: #333;
}

h2 {
  text-align: center;
  margin: 20px 0;
  color: #2c3e50;
}

/* Container Grid */
.restaurant-list {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Konsisten 5 kolom */
  gap: 20px;
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  box-sizing: border-box; /* Hitung padding ke dalam lebar */
}

.restaurant-actions {
  display: flex;
  justify-content: space-between; /* Membuat dua kolom terpisah */
  align-items: center;
}


/* Restaurant Item Styles */
.restaurant-item {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
  cursor: pointer;
  display: flex;
  flex-direction: column;
}

.restaurant-item:hover {
  transform: translateY(-5px);
}

/* Image Container and Styling */
.restaurant-image-container {
  position: relative; /* Buat posisi relatif untuk overlay kota */
  width: 100%;
  height: 200px;
  overflow: hidden;
}

.restaurant-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

/* Kota Overlay */
.restaurant-city {
  position: absolute;
  top: 10px;
  left: 10px;
  background-color: rgba(0, 0, 0, 0.7); /* Transparansi */
  color: #fff;
  font-size: 0.9rem;
  font-weight: bold;
  padding: 5px 10px;
  border-radius: 4px;
  z-index: 2; /* Pastikan di atas gambar */
}

/* Info Section */
.restaurant-info {
  padding: 16px;
}

.restaurant-name {
  font-size: 1.2rem;
  font-weight: bold;
  color: #34495e;
  margin: 0;
}

.restaurant-description-card {
  margin: 8px 0;
  color: #555;
}

.restaurant-description {
  font-size: 0.9rem;
  color: #7f8c8d;
  padding: 15px;
  margin: 0;
}

/* Rating di Sebelah Kiri */
.restaurant-rating {
  display: flex;
  align-items: center;
  font-size: 1rem;
}

.restaurant-rating .star {
  font-size: 1rem;
  color: #f39c12; /* Warna bintang kuning */
}

.restaurant-actions > .restaurant-rating {
  margin-right: 20px; /* Beri jarak antara rating dan tombol hapus */
}

/* Rating angka */
.restaurant-rating span {
  font-size: 0.9rem;
  color: #34495e;
}

/* Align all card contents */
.restaurant-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Menjaga isi rapi */
  height: 100%; /* Pastikan semua item punya tinggi yang sama */
}

/* Responsive Design */
@media (max-width: 768px) {
  .restaurant-list {
    grid-template-columns: repeat(2, 1fr); /* 2 kolom untuk layar sedang */
  }

  .restaurant-info {
    padding: 10px;
  }
}

@media (max-width: 480px) {
  .restaurant-list {
    grid-template-columns: 1fr; /* 1 kolom untuk layar kecil */
  }

  .restaurant-rating {
    margin-top: 5px;
  }
}

/* Button */
/* Remove Favorite Button */
.remove-favorite-button {
  background: none;
  border: none;
  font-size: 1.2rem; /* Ukuran ikon */
  cursor: pointer;
  transition: transform 0.2s ease, color 0.2s ease;
  align-self: flex-end; /* Tombol hapus di sebelah kanan */
  padding: 10px;
  min-width: 44px; /* Menetapkan lebar minimal */
  min-height: 44px; /* Menetapkan tinggi minimal */
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%; /* Tombol berbentuk lingkaran jika diinginkan */
}

.remove-favorite-button:hover {
  color: #c0392b; /* Warna lebih gelap saat hover */
  transform: scale(1.2); /* Efek zoom */
}

/* Responsive Design */
@media (max-width: 768px) {
  .restaurant-list {
    grid-template-columns: repeat(2, 1fr); /* 2 kolom untuk layar sedang */
  }

  .restaurant-info {
    padding: 10px;
  }

  .remove-favorite-button {
    padding: 12px; /* Memberikan padding lebih besar jika di layar lebih kecil */
    font-size: 1.2rem;
  }
}

@media (max-width: 480px) {
  .restaurant-list {
    grid-template-columns: 1fr; /* 1 kolom untuk layar kecil */
  }

  .restaurant-image-container {
    height: 150px;
  }

  .restaurant-name {
    font-size: 1rem;
  }

  .restaurant-description {
    font-size: 0.8rem;
  }
}
